import React from "react";
import contactMe from "./contactMe.png";

function Contact() {
  return (
    <div>
      <section id="showcase">
        <div className="container">
          <img src={contactMe} alt="Smiley face" height="100" />
          <form className="my-form">
            <div class="form-group">
              <label>Name: </label>
              <input type="text" name="name" />
              <label>Email: </label>
              <input type="text" name="email" />
              <label>Message: </label>
              <textarea name="message"></textarea>
            </div>
            <br />
            <button>Submit</button>
          </form>
        </div>
      </section>
      <p align="center">A friendly hello, a business proposal, a new opportunity or kind suggestions</p>
    </div>
  );
}

export default Contact;
